@import "../../../styles/variables.module.scss";
.advertisingSpot {
  width: 100%;
  padding: 64px 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  @media screen and (min-width: $sm) {
    padding: 64px 0px;
    img {
      max-width: 480px;
    }
  }
  @media screen and (min-width: $md) {
    padding: 96px 0px;
    img {
      max-width: 640px;
    }
  }
  @media screen and (min-width: $lg) {
    padding: 128px 0px;
    img {
      max-width: 480px;
    }
  }
  @media screen and (min-width: $xl) {
    padding: 128px 0px;
    img {
      max-width: 640px;
    }
  }
  @media screen and (min-width: $xxl) {
    img {
      max-width: 680px;
    }
  }
}
