@import './layout.scss';
// @import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&display=swap');
// @import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap');

$primaryFont: 'Open Sans', sans-serif;
$secondaryFont: 'Raleway', sans-serif;

p {
  font-size: 1.125rem;
  line-height: 2rem;
  font-family: $primaryFont;
}
h3 {
  font-size: 1.25rem;
  line-height: 2rem;
  font-weight: 600;
  text-transform: capitalize;
  font-family: $secondaryFont;
}
h2 {
  font-size: 2rem;
  line-height: 2.5rem;
  font-weight: 400;
  text-transform: uppercase;
  font-family: $secondaryFont;
}
h1 {
  font-size: 2.5rem;
  line-height: 3rem;
  font-weight: 600;
  text-transform: capitalize;
  font-family: $secondaryFont;
}
.small {
  font-size: 0.75rem;
  line-height: 1rem;
  font-weight: 600;
  letter-spacing: 0.045rem;
  text-transform: uppercase;
}
.display {
  font-size: 6rem;
  line-height: 6rem;
}

@media (min-width: $xl) {
  p {
    font-size: 1.25rem;
    line-height: 2rem;
  }
  h3 {
    font-size: 1.5rem;
    line-height: 2rem;
  }
  h2 {
    font-size: 2.5rem;
    line-height: 3rem;
  }
  h1 {
    font-size: 4rem;
    line-height: 4.5rem;
  }
  .small {
    font-size: 0.875rem;
    line-height: 1rem;
    letter-spacing: 0.0525rem;
  }
  .display {
    font-size: 8rem;
    line-height: 8rem;
  }
}
