@import "../../../styles/variables.module.scss";
.portfolioItem {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 64px 0;
  gap: 32px;
  position: relative;
  @media screen and(min-width:$md) {
    gap: 48px;
    padding: 64px 0;
  }
  @media screen and(min-width:$lg) {
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 0px;
    padding: 128px 0;
    &:last-of-type {
      padding-bottom: 0;
    }
  }
}

.logoDeliverables {
  width: 100%;
  padding: 0 16px;
  order: 3;
  display: flex;
  flex-direction: column;
  gap: 32px;
  @media screen and (min-width: $sm) {
    padding: 0 32px;
  }
  @media screen and (min-width: $md) {
    padding: 0 64px;
    width: 768px;
    gap: 48px;
  }
  @media screen and(min-width:$lg) {
    width: 544px;
    padding: 0 32px;
    order: 2;
    gap: 64px;
  }
  @media screen and(min-width:$xl) {
    width: 768px;
    padding: 0 64px;
  }
  @media screen and(min-width:$xxl) {
    width: 808px;
  }
}
