@import "../../../styles/variables.module.scss";
.footer {
  width: 100%;
  background-color: $primaryColor;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 64px 0;
  @media screen and (min-width: $md) {
    padding: 96px 0;
    gap: 96px;
  }
  @media screen and(min-width:$lg) {
    padding: 128px 0;
    gap: 64px;
  }
}

.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0 16px;
  gap: 64px;
  @media screen and (min-width: $sm) {
    padding: 0 32px;
  }
  @media screen and (min-width: $md) {
    padding: 0 64px;
    width: 768px;
  }
  @media screen and (min-width: $lg) {
    width: 1088px;
    padding: 0;
  }
  @media screen and (min-width: $xl) {
    width: 1536px;
  }
  @media screen and (min-width: $xxl) {
    width: 1616px;
  }
}

.footerBody {
  display: flex;
  flex-direction: column;
  gap: 32px;
  @media screen and (min-width: $md) {
    gap: 48px;
  }
  @media screen and (min-width: $lg) {
    flex-direction: row;
    justify-content: space-between;
  }
}
