@import '../../../styles/variables.module.scss';
.quickLinks {
  display: flex;
  flex-direction: column;
  gap: 16px;
  a {
    color: $secondaryColor;
    &:hover {
      cursor: pointer;
      color: $secondaryColor200;
    }
  }
  p:not(:first-child) {
    color: $secondaryColor;
    &:hover {
      cursor: pointer;
      color: $secondaryColor200;
    }
  }
  p {
    &:nth-child(2) {
      text-transform: capitalize;
    }
    &:nth-child(3) {
      color: $primaryColor200;
      font-size: small;
      max-width: 32ch;
      line-height: 120%;
    }
    &:last-child {
      text-transform: uppercase;
    }
  }
}
