@import "../../../styles/variables.module.scss";
.themeSwitch {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.themeSwitchBody {
  display: flex;
  gap: 16px;
}

.switcherLight {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0px 2px;
  gap: 10px;
  width: 56px;
  height: 24px;
  border-radius: 100px;
  background-color: $secondaryColor;
}

.switcherDark {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0px 2px;
  gap: 10px;
  width: 56px;
  height: 24px;
  border-radius: 100px;
  background-color: $primaryColor800;
}

.knob {
  width: 22px;
  height: 22px;
  border-radius: 50%;
  &:hover {
    cursor: pointer;
  }
}

.knobLight {
  background-color: $primaryColor;
  margin-right: auto;
}

.knobDark {
  background-color: $secondaryColor100;
  margin-left: auto;
}
