@import '../../../styles/variables.module.scss';

.landingScreen {
  width: 100%;
  height: var(--doc-height);
  padding-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-image: url('../../../assets/images/imnot__busStopBillboard__small.webp');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: rgba($color: #000000, $alpha: 0.85);
  background-blend-mode: overlay;
  @media screen and (min-width: $md) {
    background-image: url('../../../assets/images/imnot__busStopBillboard__medium.webp');
    padding-bottom: 96px;
  }
  @media screen and (min-width: $lg) {
    background-image: url('../../../assets/images/imnot__busStopBillboard__large.webp');
    padding-bottom: 64px;
  }
  @media screen and (min-width: $xl) {
    padding-bottom: 128px;
  }
}
