@import "../../../styles/variables.module.scss";

a {
  text-decoration: none;
  button {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 1rem 2rem;
    gap: 1rem;
    border-style: solid;
    border-width: 1px;
    cursor: pointer;
    transition: all 300ms;
    user-select: none;
    font-weight: 600;
    text-transform: capitalize;
    @media screen and (min-width: $sm) {
      width: auto;
    }
    &:active {
      opacity: 0.7;
    }
    &:disabled {
      opacity: 0.5;
      pointer-events: none;
    }
    &:hover {
      box-shadow: 0 0 32px 12px rgba(48, 52, 54, 0.15);
    }
  }
}

.buttonSolidPrimary {
  &[data-theme="light"] {
    background-color: $secondaryColor;
    color: $primaryColor;
    border-color: $secondaryColor;
    svg {
      path {
        stroke: $primaryColor;
      }
    }
    &:hover {
      border-color: $primaryColor;
    }
  }

  &[data-theme="dark"] {
    background-color: $primaryColor;
    color: $secondaryColor;
    border-color: $secondaryColor;
    svg {
      path {
        stroke: $secondaryColor;
      }
    }
    &:hover {
      border-color: $secondaryColor200;
      color: $secondaryColor200;
      svg {
        path {
          stroke: $secondaryColor200;
        }
      }
    }
  }
}

.buttonSolidSecondary {
  background-color: $primaryColor;
  color: $primaryColor100;
  border-color: $primaryColor100;
  svg {
    path {
      stroke: $primaryColor100;
    }
  }
  &:hover {
    border-color: $secondaryColor200;
    color: $secondaryColor200;
    svg {
      path {
        stroke: $secondaryColor200;
      }
    }
  }
}
