@import "../../../styles/variables.module.scss";

.articleCTA {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.articleCTAIntro {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  margin-top: 64px;
  color: $primaryColor100;
  p:first-child {
    color: $secondaryColor300 !important;
  }
  @media screen and (min-width: $sm) {
    margin-top: 96px;
  }
  @media screen and (min-width: $md) {
    margin-top: 0;
  }
  @media screen and (min-width: $lg) {
    width: 50%;
    padding: 0 32px;
  }
  @media screen and (min-width: $xl) {
    padding: 0 64px;
  }
}

.articleCTAProjects {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  @media screen and (min-width: $sm) {
    margin-top: 96px;
  }
  @media screen and (min-width: $md) {
    margin-top: 0;
  }
  @media screen and (min-width: $lg) {
    width: 50%;
    padding: 0 32px;
    margin-left: auto;
  }
  @media screen and (min-width: $xl) {
    padding: 0 64px;
  }
}

.articleCTAEstimate {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  padding: 64px 16px;
  color: $primaryColor;
  @media screen and (min-width: $sm) {
    padding: 64px 0px;
    width: 544px;
  }
  @media screen and (min-width: $md) {
    padding: 96px 0px;
    width: 640px;
  }
  @media screen and (min-width: $lg) {
    padding: 128px 0px;
    width: 480px;
  }
  @media screen and (min-width: $xl) {
    padding: 128px 0px;
    width: 640px;
  }
  @media screen and (min-width: $xxl) {
    width: 680px;
  }
  p:first-child {
    color: $primaryColor700 !important;
  }
  button {
    background-color: $primaryColor;
    color: $primaryColor100;
    border-color: $primaryColor;
    svg {
      path {
        stroke: $primaryColor100;
      }
    }
    &:hover {
      border-color: $secondaryColor200;
      color: $secondaryColor200;
      svg {
        path {
          stroke: $secondaryColor200;
        }
      }
    }
  }
}

.articleCTAProcess {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  padding: 64px 16px;
  background-color: $primaryColor700;
  color: $primaryColor100;
  p:first-child {
    color: $primaryColor300 !important;
  }
  @media screen and (min-width: $sm) {
    padding: 64px 0px;
    width: 544px;
  }
  @media screen and (min-width: $md) {
    padding: 96px 0px;
    width: 640px;
  }
  @media screen and (min-width: $lg) {
    padding: 128px 0px;
    width: 480px;
  }
  @media screen and (min-width: $xl) {
    padding: 128px 0px;
    width: 640px;
  }
  @media screen and (min-width: $xxl) {
    width: 680px;
  }
}
